<template>
  <div id="exchange">

    <div>
      <Nav page="exchange" />
      <Banner text="产品"/>

      <!-- 正文内容 -->

      <div class="mainContent" v-loading="load">

        <div class="title flex align between">
          <h2>IEO</h2>

          <el-tooltip class="item" effect="dark" content="" placement="top-start">
            <div class="help cursor"><i></i>&nbsp;认购帮助</div>
          </el-tooltip>
          
        </div>
        <!-- title -->


        <div class="proBox">
            <ProItem v-for="(item,index) in productList" :key="index" :item="item"/>
        </div>
        <!-- proBox -->

      </div>
      <!-- mainContent -->

      <!-- 正文结束 -->
      <Footer />
    </div>
    <!-- filter -->


  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'
  import {
    getProductList
  } from '../data/apis';

  import Nav from './nav'
  import Banner from '@com/exchange/banner'
  import Footer from './footer'
  import ProItem from '@com/exchange/proItem'


  export default {
    data() {
      return {
        productList:[],
        load:false
      }
    },
    components: {
      Nav,
      Banner,
      Footer,
      ProItem
    },
    methods: {
      getData(){
        this.load = true;
        getProductList().then(v=>{
          this.productList = v.data;
          this.load = false;
        })
      }//getData
    },
    mounted() {
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .mainContent {
    min-height: 300px;
    >.title {
      margin-top: 50px;
      margin-bottom: 40px;

      h2 {
        font-size: 30px;
        font-weight: bold;
      }

      .help {
        color: $them_color_gray_light;
        font-size: 16px;

        i {
          display: inline-block;
          width: 25px;
          height: 25px;
          vertical-align: middle;
          margin-top: -2px;
          background: url('../assets/img/help@2x.png') center no-repeat;
          background-size: 100% 100%;
        }
      }
    }

    .proItem {
      padding: 0 0;
      overflow: hidden;
      margin-bottom: 50px;

      .img {
        width: 400px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }

      .rightContent {
        flex: 1;
        padding: 20px 50px;

        .pair {
          .row {
            background: url('../assets/img/rightG.png') center no-repeat;
            background-size: 100% auto;
            width: 60px;
            height: 30px;
            margin: 0 25px;
          }

          .iconImg {
            width: 35px;
            height: 35px;
          }

          span {
            font-size: 25px;
            font-weight: 500;
            margin-left: 10px;
          }
        }

        .goBtn {
          border: 1px solid $them_color;
          display: block;
          cursor: pointer;
          color: $them_color;
          font-size: 16px;
          padding: 10px 20px;
          font-weight: 600;
          border-radius: 2px;
          font-weight: normal;

          i {
            display: inline-block;
            width: 25px;
            height: 15px;
            background: url('../assets/img/right@2x.png') center no-repeat;
            background-size: 100% auto;
            position: relative;
            top: 1px;
            margin-left: 10px;
          }
        }

        .pText {
          color: $them_color_gray_light;
          font-size: 14px;
          display: block;
          padding: 30px 0;
          border-bottom: 1px solid #E9E9E9;
          margin-bottom: 10px;
        }

        .dataGroup {
          .item {
            width: 33%;
            margin-top: 20px;

            b {
              display: block;
              font-size: 20px;
              font-weight: normal;

              span {
                font-size: 14px;
              }
            }

            small {
              color: $them_color_gray_light;
              font-size: 14px;
            }
          }
        }
      }
    }
  }

</style>



